.no-webp .inicio {
  background-image: url(/build/img/pexels-shvets-production-7946821.jpg);
}

.webp .inicio {
  background-image: url(/build/img/pexels-shvets-production-7946821.webp);
}

.header {
  background-color: $grisOscuro;
  padding: 1rem 0 3rem 0;

  &.inicio {
    background-position: center center;
    background-size: cover;

    @include tablet {
      min-height: 45rem;
    }
    .contenido-header {
      @include tablet {
        min-height: 45rem;
      }
    }
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  h1 {
    color: $blanco;
    text-align: left;
    font-weight: $bold;
    line-height: 2;
  }
}
